<template>
  <div class="step-wrap" id="step1">
    <el-tabs v-model="currentTabName" :closable="closable" @tab-remove="removeTab">
      <el-tab-pane
        v-for="item in editableTabsList"
        :key="item.sort"
        :label="item.suspectName"
        :name="item.sort"
      >
      <div class="step-body-wrap">
        <el-form ref="form" :label-position="labelPosition" :model="item.contentForm" label-width="80px">
        <el-row>
          <el-col :span="8" style="padding-right:40px;">
            <div class="">
              <el-form-item label="姓名" title="姓名">
                <el-input v-model="item.suspectName" maxlength="50"></el-input>
              </el-form-item>

              <el-form-item label="是否系生活不能自理的唯一抚养人" title="是否系生活不能自理的唯一抚养人">
                <el-select v-model="item.supporterOfTheDisabled" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="是否成年人" title="是否成年人">
                <el-select v-model="item.suspectAdults" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>

            </div>
          </el-col>
          <el-col :span="8" style="padding:0 20px;">
            <div class="">
              <el-form-item label="是否有重大疾病" title="是否有重大疾病">
                <el-select v-model="item.suspectMajorDisease" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="是否在逃" title="是否在逃">
                <el-select v-model="item.suspectEscape" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>

            </div>
          </el-col>
          <el-col :span="8" style="padding-left:40px;">
            <div class="">
              <el-form-item label="是否怀孕、哺乳婴儿的妇女" title="是否怀孕、哺乳婴儿的妇女">
                <el-select v-model="item.suspectPregnantWoman" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="是否追捕的犯罪嫌疑人" title="是否追捕的犯罪嫌疑人">
                <el-select v-model="item.suspectChase" placeholder="">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>
            </div>
          </el-col>
        </el-row>

        <el-row>
          <h3 class="h3-title">
            <p style="font-size:14px;color:#333;font-weight:400;margin-bottom:0;">基本信息快速填充</p>
            <small style="font-size:12px;color:#999;font-weight:400;margin-bottom:0;">系统自动获取嫌疑人基本信息在下表，并完成上方表单的填充，您也可以手动修改</small>
            <span class="btn-wrap"><el-button size="small" @click="addSubTab(currentTabName)" style="color:#42a0f8">确定</el-button></span>
          </h3>
          <el-form-item label="">
            <el-input type="textarea" maxlength="2048" rows="10" v-model="item.suspectBaseInfo" style="font-size:14px;color:#999;margin-bottom:10px;"></el-input>
          </el-form-item>
        </el-row>
        </el-form>
      </div>
      </el-tab-pane>
    </el-tabs>
    <el-dialog
      :visible.sync="centerDialogVisible"
      width="550px"
      :close-on-click-modal="closeDialog"
      center v-dialogDrag>
      <div class="yofc-dialog-header" slot="title">
         <h1>添加嫌疑人</h1>
      </div>
      <p>系统检测到有新的嫌疑人信息,如需导入请选择并点击确定按钮 <a href="javascript:void(0)" style="float:right"  v-bind:title="checkAll" @click="handleCheckAllChange">全选</a></p>
      <div>
        <span v-for="(item,index) in checkSuspectInfoFromList" :key="index" style="display:inline-block;width:100px;margin-bottom:20px;">
          <input type="checkbox" v-model="item.isChecked"  @change="handleCheckedOneChange(item)">
          <em v-bind:title="item.suspectName" style="vertical-align: middle;font-style: normal">{{item.suspectName}}</em>
          <!-- <em style="display:inline-block;width:20px;height:14px"></em> -->
        </span>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="canel()">取 消</el-button>
        <el-button size="small" type="primary"  @click="confirm()">确 定</el-button>
      </span>
    </el-dialog>
    <el-button class="addBtn" type="text" @click="addTab(currentTabName)"><i class="iconfont" title="新增嫌疑人">&#xe6ed;</i></el-button>
    <div class="step-footer-wrap">
      <el-button size="small" @click="onSubmit()">保存</el-button>
      <el-button size="small" type="primary" @click="next()">下一步</el-button>
    </div>
  </div>
</template>

<script>
import { getAjaxPromise } from '@/api/admin'
// import _ from 'lodash'
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: 'step1',
  data() {
    return {
      msg: 'step1',
      tabIndex: 1,
      // 是否可以关闭tab
      closable: false,
      currentTabName: '1',
      labelPosition: 'top',
      centerDialogVisible: false,
      closeDialog: false,
      checkSuspectInfoFromList: [],
      checkedInfo: '',
      checkAll: false,
      // changeRouterFlag: false,
      editableTabsList: [
        {
          // title: '嫌疑人1',
          caseNumber: this.caseObj.caseNumber,
          sort: '1',
          // 嫌疑人姓名
          suspectName: '嫌疑人',
          // 是否成年人
          suspectAdults: '否',
          // 是否系生活不能自理的唯一抚养人
          supporterOfTheDisabled: '否',
          // 是否有重大疾病
          suspectMajorDisease: '否',
          // 是否在逃
          suspectEscape: '否',
          // 是否怀孕、哺乳婴儿的妇女
          suspectPregnantWoman: '否',
          // 是否追捕的犯罪嫌疑人
          suspectChase: '否',
          // 基本信息快速填充
          suspectBaseInfo: ''
        }
      ],
      copyEditableTabsList: [
        {
          // title: '嫌疑人1',
          caseNumber: this.caseObj.caseNumber,
          sort: '1',
          // 嫌疑人姓名
          suspectName: '嫌疑人',
          // 是否成年人
          suspectAdults: '否',
          // 是否系生活不能自理的唯一抚养人
          supporterOfTheDisabled: '否',
          // 是否有重大疾病
          suspectMajorDisease: '否',
          // 是否在逃
          suspectEscape: '否',
          // 是否怀孕、哺乳婴儿的妇女
          suspectPregnantWoman: '否',
          // 是否追捕的犯罪嫌疑人
          suspectChase: '否',
          // 基本信息快速填充
          suspectBaseInfo: ''
        }
      ],
      saveFlag: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      window.checkIsSave = (path, close) => {
        this.next(path, close)
      }
      // this.changeRouterFlag = false
      this.editableTabsList1 = Object.assign([], this.editableTabsList)
      this.getSuspectsBaseInfo()
    })
  },
  methods: {
    handleCheckAllChange(value) {
      this.checkAll = !this.checkAll
      if (this.checkAll) {
        this.checkSuspectInfoFromList.forEach((item, index) => {
          item.isChecked = true
        })
      } else {
        this.checkSuspectInfoFromList.forEach((item, index) => {
          item.isChecked = false
        })
      }
    },
    handleCheckedOneChange(item) {
      this.checkSuspectInfoFromList.forEach((item1, index) => {
        if (item1.suspectIdNumber === item.suspectIdNumber) {
          item1.isChecked = item.isChecked
        }
      })
      if (item.isChecked) {
        let i = 0
        this.checkSuspectInfoFromList.forEach((item, index) => {
          if (item.isChecked === true) {
            i = i + 1
          }
        })
        if (i === this.checkSuspectInfoFromList.length) {
          this.checkAll = true
        }
      } else {
        let j = this.checkSuspectInfoFromList.length
        this.checkSuspectInfoFromList.forEach((item, index) => {
          if (!item.isChecked) {
            j = j - 1
          }
        })
        if (this.checkSuspectInfoFromList.length > j) {
          this.checkAll = false
        }
      }
    },
    canel() {
      this.centerDialogVisible = false
      this.closable = true
      let newTabName =
        parseInt(this.editableTabsList[this.editableTabsList.length - 1].sort) +
        1 +
        ''
      this.editableTabsList.push({
        // title: '嫌疑人' + newTabName,
        caseNumber: this.caseObj.caseNumber,
        sort: newTabName,
        // 嫌疑人姓名
        suspectName: '嫌疑人',
        // 是否成年人
        suspectAdults: '否',
        // 是否系生活不能自理的唯一抚养人
        supporterOfTheDisabled: '否',
        // 是否有重大疾病
        suspectMajorDisease: '否',
        // 是否在逃
        suspectEscape: '否',
        // 是否怀孕、哺乳婴儿的妇女
        suspectPregnantWoman: '否',
        // 是否追捕的犯罪嫌疑人
        suspectChase: '否',
        // 基本信息快速填充
        suspectBaseInfo: ''
      })
      this.currentTabName = newTabName
    },
    confirm() {
      let i = 0
      let newTabName = ''
      this.checkSuspectInfoFromList.forEach((item, index) => {
        if (item.suspectAdults === '') {
          item.suspectAdults = '否'
        }
        if (item.supporterOfTheDisabled === '') {
          item.supporterOfTheDisabled = '否'
        }
        if (item.suspectEscape === '') {
          item.suspectEscape = '否'
        }
        if (item.suspectPregnantWoman === '') {
          item.suspectPregnantWoman = '否'
        }
        if (item.suspectChase === '') {
          item.suspectChase = '否'
        }
        if (item.suspectMajorDisease === '') {
          item.suspectMajorDisease = '否'
        }
        if (item.isChecked) {
          newTabName =
            parseInt(
              this.editableTabsList[this.editableTabsList.length - 1].sort
            ) +
            1 +
            ''
          i = i + 1

          item.sort = newTabName
          this.editableTabsList.push(item)
        }
      })
      if (i === 0) {
        this.$message({
          message: '请选择要添加的嫌疑人',
          type: 'warning',
          duration: 2000
        })
        return
      } else {
        this.centerDialogVisible = false
      }
      this.closable = true
      this.currentTabName = newTabName
    },
    next(path, close) {
      // 判断案件信息时候是否有修改
      let flag = this.isObjEqual(
        this.copyEditableTabsList,
        this.editableTabsList
      )
      if (flag) {
        if (close) {
          if (eDossier) {
            eDossier.close()
          }
        } else {
          if (path) {
            this.$router.push({ path: path })
          } else {
            this.$router.push({ name: 'step2' })
          }
        }
      } else {
        this.$confirm('有修改的嫌疑人信息未保存, 是否保存?', '', {
          confirmButtonText: '保存',
          cancelButtonText: '取消',
          closeOnClickModal: false,
          center: true,
          showClose: false
        })
          .then(() => {
            this.onSubmit(path, close)
          })
          .catch(() => {
            if (close) {
              if (eDossier) {
                eDossier.close()
              }
            } else {
              if (path) {
                this.$router.push({ path: path })
              } else {
                this.$router.push({ name: 'step2' })
              }
            }
          })
      }
    },
    // 表单提交
    onSubmit(path, close) {
      let flag = true
      this.editableTabsList.forEach((item, index) => {
        if (item.suspectName === '') {
          flag = false
          this.$message({
            message: '犯罪嫌疑人姓名不能不空',
            type: 'warning',
            duration: 2000
          })
          return false
        }
      })
      if (flag) {
        // 保存或更新嫌疑人基本信息(全部嫌疑人信息的保存)
        this.saveOrUpdateSuspectsBaseInfo(path, close)
      }
    },

    // 增加嫌疑人tab
    addTab(targetName) {
      const param = {
        caseNumber: this.caseObj.caseNumber,
        userId: localCache.getItem('userId')
      }
      const checkSuspectInfoFromJzDataPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/suspectsBaseInfo/checkSuspectInfoFromJzData',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })
      checkSuspectInfoFromJzDataPromise.then(res => {
        if (res.data.code === '200') {
          if (res.data.data.length > 0) {
            this.centerDialogVisible = true
            this.checkSuspectInfoFromList = res.data.data
            this.checkSuspectInfoFromList.forEach((item, index) => {
              item.isChecked = false
            })
          } else {
            let newTabName =
              parseInt(
                this.editableTabsList[this.editableTabsList.length - 1].sort
              ) +
              1 +
              ''
            this.editableTabsList.push({
              // title: '嫌疑人' + newTabName,
              caseNumber: this.caseObj.caseNumber,
              sort: newTabName,
              // 嫌疑人姓名
              suspectName: '嫌疑人',
              // 是否成年人
              suspectAdults: '否',
              // 是否系生活不能自理的唯一抚养人
              supporterOfTheDisabled: '否',
              // 是否有重大疾病
              suspectMajorDisease: '否',
              // 是否在逃
              suspectEscape: '否',
              // 是否怀孕、哺乳婴儿的妇女
              suspectPregnantWoman: '否',
              // 是否追捕的犯罪嫌疑人
              suspectChase: '否',
              // 基本信息快速填充
              suspectBaseInfo: ''
            })
            this.closable = true
            this.currentTabName = newTabName
          }
        }
      })
    },

    // 删除嫌疑人tab
    removeTab(targetName) {
      let tabs = this.editableTabsList
      if (tabs.length === 2) {
        this.closable = false
      }
      let activeName = this.currentTabName
      if (activeName === targetName) {
        tabs.forEach((tab, index) => {
          if (tab.sort === targetName) {
            let nextTab = tabs[index + 1] || tabs[index - 1]
            if (nextTab) {
              activeName = nextTab.sort
            }
          }
        })
      }

      this.currentTabName = activeName
      this.editableTabsList = tabs.filter(tab => tab.sort !== targetName)
    },
    // 获取犯罪嫌疑人基本信息
    getSuspectsBaseInfo() {
      const param = {
        caseNumber: this.caseObj.caseNumber,
        userId: localCache.getItem('userId')
      }
      const baseInfoPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/suspectsBaseInfo/getSuspectsBaseInfo',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })
      baseInfoPromise.then(res => {
        this.copyEditableTabsList = []
        if (
          res.data.code === '200' &&
          res.data.data &&
          res.data.data.length > 0
        ) {
          this.editableTabsList = res.data.data
          if (this.saveFlag) {
            // 第一次查询默认保存一次
            this.saveOrUpdateSuspectsBaseInfo()
          }
          if (this.currentTabName === '1') {
            this.currentTabName = this.editableTabsList[0].sort
          }
          if (this.editableTabsList.length === 1) {
            this.closable = false
          } else {
            this.closable = true
          }
          this.tabIndex = this.editableTabsList.length
          this.editableTabsList.forEach((item, index) => {
            // item.sort = '' + item.sort
            if (item.suspectName === '') {
              item.suspectName = '嫌疑人'
            }
            if (item.suspectAdults === '') {
              item.suspectAdults = '否'
            }
            if (item.supporterOfTheDisabled === '') {
              item.supporterOfTheDisabled = '否'
            }
            if (item.suspectEscape === '') {
              item.suspectEscape = '否'
            }
            if (item.suspectPregnantWoman === '') {
              item.suspectPregnantWoman = '否'
            }
            if (item.suspectChase === '') {
              item.suspectChase = '否'
            }
            if (item.suspectMajorDisease === '') {
              item.suspectMajorDisease = '否'
            }
            this.copyEditableTabsList.push(Object.assign({}, item))
          })
        } else if (res.data.code === '200' && !res.data.data) {
          this.closable = false
          this.copyEditableTabsList = [
            {
              // title: '嫌疑人1',
              caseNumber: this.caseObj.caseNumber,
              sort: '1',
              // 嫌疑人姓名
              suspectName: '嫌疑人',
              // 是否成年人
              suspectAdults: '否',
              // 是否系生活不能自理的唯一抚养人
              supporterOfTheDisabled: '否',
              // 是否有重大疾病
              suspectMajorDisease: '否',
              // 是否在逃
              suspectEscape: '否',
              // 是否怀孕、哺乳婴儿的妇女
              suspectPregnantWoman: '否',
              // 是否追捕的犯罪嫌疑人
              suspectChase: '否',
              // 基本信息快速填充
              suspectBaseInfo: ''
            }
          ]
        } else if (res.data.code === '500') {
          this.closable = false
          this.copyEditableTabsList = [
            {
              // title: '嫌疑人1',
              caseNumber: this.caseObj.caseNumber,
              sort: '1',
              // 嫌疑人姓名
              suspectName: '嫌疑人',
              // 是否成年人
              suspectAdults: '否',
              // 是否系生活不能自理的唯一抚养人
              supporterOfTheDisabled: '否',
              // 是否有重大疾病
              suspectMajorDisease: '否',
              // 是否在逃
              suspectEscape: '否',
              // 是否怀孕、哺乳婴儿的妇女
              suspectPregnantWoman: '否',
              // 是否追捕的犯罪嫌疑人
              suspectChase: '否',
              // 基本信息快速填充
              suspectBaseInfo: ''
            }
          ]
          this.$message({
            message: res.data.msg,
            type: 'error',
            duration: 2000
          })
        } else if (
          res.data.code === '200' &&
          res.data.data &&
          res.data.data.length === 0
        ) {
          this.closable = false
          this.copyEditableTabsList = [
            {
              // title: '嫌疑人1',
              caseNumber: this.caseObj.caseNumber,
              sort: '1',
              // 嫌疑人姓名
              suspectName: '嫌疑人',
              // 是否成年人
              suspectAdults: '否',
              // 是否系生活不能自理的唯一抚养人
              supporterOfTheDisabled: '否',
              // 是否有重大疾病
              suspectMajorDisease: '否',
              // 是否在逃
              suspectEscape: '否',
              // 是否怀孕、哺乳婴儿的妇女
              suspectPregnantWoman: '否',
              // 是否追捕的犯罪嫌疑人
              suspectChase: '否',
              // 基本信息快速填充
              suspectBaseInfo: ''
            }
          ]
          // this.changeRouterFlag = true
        }
      })
    },
    // 保存或更新嫌疑人基本信息(全部嫌疑人信息的保存)
    saveOrUpdateSuspectsBaseInfo(path, close) {
      if (!localCache.getItem('userId')) {
        this.$message({
          message: 'userId为空',
          type: 'error',
          duration: 2000
        })
        return
      }
      this.editableTabsList.forEach((item, index) => {
        item.createUser = localCache.getItem('userId')
        // item.updateUser = localCache.getItem('userId')
      })
      let param = {
        suspectsBaseInfoVos: this.editableTabsList
      }
      const saveOrUpdateSuspectsBaseInfoPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/suspectsBaseInfo/saveOrUpdateSuspectsBaseInfos',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })
      saveOrUpdateSuspectsBaseInfoPromise.then(res => {
        // this.changeRouterFlag = true
        if (res.data.code === '200') {
          if (!this.saveFlag) {
            this.$message({
              message: '保存犯罪嫌疑人基本信息成功',
              type: 'success',
              duration: 2000
            })
          }
          this.saveFlag = false
          this.getSuspectsBaseInfo()
          if (close) {
            if (eDossier) {
              eDossier.close()
            }
          } else {
            if (path) {
              this.$router.push({ path: path })
            }
          }
        } else {
          if (!this.saveFlag) {
            this.$message({
              message: res.data.msg,
              type: 'success',
              duration: 2000
            })
          }
          this.saveFlag = false
        }
      })
    },
    // 数据抽取
    addSubTab(currentTabName) {
      let thisSuspectBaseInfo = ''
      this.editableTabsList.forEach((item, index) => {
        if (item.sort === currentTabName) {
          thisSuspectBaseInfo = item.suspectBaseInfo
        }
      })
      if (!thisSuspectBaseInfo || thisSuspectBaseInfo === '') {
        this.$message({
          message: '嫌疑人基本信息不能为空',
          type: 'warning',
          duration: 2000
        })
        return
      }
      let param = {
        suspectBaseInfo: thisSuspectBaseInfo
      }
      // param = {
      //   suspectBaseInfo:
      //     '犯罪嫌疑人许为华，外号“华华”，男，汉族，中共党员，1982年10月20日生，中专文化程度，身份证号：420114198210203710,户籍所在地：武汉市蔡甸区蔡甸街长喜里89号2楼2号，现住地：武汉市蔡甸区蔡甸街长喜里89号2楼2号，无业。2018年1月30日因涉嫌贩卖毒品罪被我局拘留，同年2月9日被武汉市蔡甸区人民检察院批准逮捕，同年2月10日被武汉市公安局蔡甸区分局执行逮捕。'
      // }
      const dataExtractionPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/suspectsBaseInfo/dataExtraction',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })
      dataExtractionPromise.then(res => {
        if (res.data.code === '200') {
          this.editableTabsList.forEach(item => {
            if (item.sort === this.currentTabName) {
              if (res.data.data.suspectName) {
                item.suspectName = res.data.data.suspectName
              }
              if (res.data.data.suspectAdults) {
                item.suspectAdults = res.data.data.suspectAdults
              }
            }
          })
          if (!res.data.data.suspectName) {
            this.$message({
              message: '请完善基本信息',
              type: 'warning',
              duration: 2000
            })
          } else {
            this.$message({
              message: '嫌疑人基本信息填充成功',
              type: 'success',
              duration: 2000
            })
          }
        } else {
          this.$message({
            message: '嫌疑人基本信息填充失败',
            type: 'error',
            duration: 2000
          })
        }
      })
    },
    isObjEqual(o1, o2) {
      let props = Object.getOwnPropertyNames(this.editableTabsList1[0])
      // let props1 = Object.getOwnPropertyNames(o1)
      // let props2 = Object.getOwnPropertyNames(o2)
      if (o1.length !== o2.length) {
        return false
      } else {
        for (let i = 0, max = 10; i < max; i++) {
          let propName = props[i]
          for (let j = 0; j < o1.length; j++) {
            if (o1[j][propName] !== o2[j][propName]) {
              return false
            }
          }
        }
      }
      return true
    }
  }
}
</script>
<style lang="scss">
.el-select-dropdown__item {
  span {
    font-size: 12px;
  }
  color: #666;
}
#step1 {
  .el-dialog {
    left: 0px;
  }
  .el-dialog__wrapper {
    overflow: hidden;
  }
  .addBtn {
    span {
      left: 19px;
      top: 4px;
    }
  }
  .el-tabs__item {
    font-weight: 600;
    &:hover {
      color: #42a0f8;
    }
  }
  .el-tabs__item:focus.is-active.is-focus:not(:active) {
    box-shadow: none;
  }
  .step-body-wrap {
    padding: 20px 24px;
  }
  .el-input {
    width: 100%;
    > .el-input__inner {
      color: #666;
      font-size: 14px;
    }
  }
  .step-footer-wrap {
    height: 64px;
    line-height: 64px;
  }
  .el-select {
    width: 100%;
    > .el-input {
      width: 100%;
    }
  }
  .el-row {
    margin-bottom: 2px;
  }

  .el-form-item {
    margin-bottom: 16px;
  }
  .el-tabs--top {
    .el-tabs__header {
      padding-left: 22px;
    }
  }

  .el-tabs__nav {
    padding: 0 8px;
  }

  .el-tabs__nav {
    padding: 0 8px;
  }

  .el-tabs__active-bar {
    padding: 0 8px;
  }

  .el-tabs__item {
    color: #333;
  }

  .el-tabs__item.is-active {
    color: #42a0f8;
  }

  .el-form-item__label {
    font-size: 14px;
    color: #333;
    padding-bottom: 0;
    font-weight: 400;
    line-height: 1.5;
    margin-bottom: 6px;
    max-width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .el-dialog__body {
    input {
      width: 14px;
      height: 14px;
      background-color: #fff;
      -webkit-appearance: none;
      border: 1px solid #c9c9c9;
      border-radius: 2px;
      outline: none;
      vertical-align: middle;
      margin-left: 0;
      &:focus,
      &:hover {
        border-color: #42a0f8;
      }
    }
    input[type='checkbox']:checked {
      background: url('../../../assets/img/gou.png') no-repeat center;
      background-color: #42a0f8;
      border: none;
    }
    em {
      display: inline-block;
      max-width: 60px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}
</style>



